body {
  margin: 0;
  font-family: sans-serif;
  background-color: #f0f0f0;
  text-align: center;
  padding: 2rem;
}

.app h1 {
  margin-bottom: 2rem;
  color: #333;
}

.btn {
  display: block;
  margin: 1rem auto;
  padding: 1rem 2rem;
  font-size: 1rem;
  background-color: #0088cc;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

.btn:hover {
  background-color: #0072a3;
}

.outside-message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: #444;
  font-size: 1.2rem;
  text-align: center;
}
